html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.dd-wrapper {
  position: relative;
  min-width: 50px; }
  .dd-wrapper .dd-header {
    display: flex;
    justify-content: space-between;
    border-radius: 5px;
    border: 1px solid #d3d3d3;
    height: 38px;
    align-items: center;
    cursor: pointer;
    user-select: none; }
    .dd-wrapper .dd-header span {
      display: flex;
      align-items: center;
      position: absolute;
      right: 5px; }
    .dd-wrapper .dd-header-title {
      font-weight: 300;
      margin: 2px 20px;
      margin-right: 30px;
      color: #666666; }
  .dd-wrapper .dd-header-title-bold {
    font-weight: 600;
    padding-left: 10px; }
  .dd-wrapper .disable-bg {
    cursor: default;
    background-color: #ebebe4;
    opacity: 0.4; }
  .dd-wrapper .dd-list {
    z-index: 10;
    position: absolute;
    width: 100%;
    border: 1px solid #f0f0f0;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    background: #fff; }
  .dd-wrapper .dd-list-item {
    height: 42px;
    line-height: 42px;
    list-style-type: none;
    position: relative;
    padding-left: 18px;
    border-bottom: solid 1px rgba(211, 211, 211, 0.42);
    cursor: pointer; }
    .dd-wrapper .dd-list-item:hover {
      background: #f8f8f8; }
    .dd-wrapper .dd-list-item:last-child {
      border-bottom: none; }
    .dd-wrapper .dd-list-item span {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 15px; }
