$AddFlightWidth: 65%;

.add-flight {
  width: $AddFlightWidth;
  width: 65%;
  display: flex;
  flex-direction: column;
  height: 765px;
  border-radius: 10px;
  border: solid 1px #e7e8f7;
  background-color: #ffffff;
  padding: 17px;
  .af-details {
    padding: 19px 22.8px 13px 15.5px;
  }
  .dd_box {
    margin-right: 15px;
  }
  .af-div {
    width: 168px;
    color: #666666;
    display: inline-block;
  }
  .af-title {
    padding-bottom: 10px;
  }

  .add_flight-info {
    display: flex;
    margin-right: 127px;
    .Add_flight_details {
      // width: 10%;
      flex: 1;
    }
    .Add_flight_details1 {
      // width: 10%;
      flex: 1;
    }
    .Add_flight_details2 {
      // width: 10%;
      flex: 1;
    }
  }

  .Dep_Arrival {
    display: flex;
    margin-top: 20px;
    margin-right: 316px;
    .Add_flight_details3 {
      // width: 10%;
      flex: 1;
    }
    .Add_flight_details4 {
      // width: 10%;
      flex: 1;
    }
  }

  .Dep_Terminal_info {
    display: flex;
    margin-top: 20px;
    margin-right: 127px;

    .Dep_Station_Details {
      flex: 1;
    }
    .Dep_Station_Details1 {
      flex: 1;
    }
    .Dep_Station_Details2 {
      flex: 1;
    }
  }

  .STD_info {
    display: flex;
    margin-top: 20px;
    margin-right: 316px;

    .STD_Details {
      flex: 1;
    }

    .STD_Details1 {
      flex: 1;
    }
  }

  .ETD_info {
    display: flex;
    margin-top: 20px;
    margin-right: 316px;

    .ETD_Details {
      flex: 1;
    }

    .ETD_Details1 {
      flex: 1;
    }
  }

  .ATD_info {
    display: flex;
    margin-top: 20px;
    margin-right: 316px;

    .ATD_Details {
      flex: 1;
    }

    .ATD_Details1 {
      flex: 1;
    }
  }

  .OFF_info {
    display: flex;
    margin-top: 20px;
    margin-right: 316px;

    .OFF_Details {
      flex: 1;
    }

    .OFF_Details1 {
      flex: 1;
    }
  }

  .Add_Flight_Header {
    height: 16px;
    font-family: Roboto;
    font-size: 14px;
    line-height: 1.14;
    color: #666666;
  }

  .Add_flight_box {
    width: 90%;
    // width:161px;
    // margin-top: 10px;
    // height: 42px;
    // margin-top: 12px;
    // border-radius: 5px;
    // border: solid 1px #d3d3d3;
    // background-color: #ffffff;
    height: 38px;
    border-radius: 5px;
    border: solid 1px #d3d3d3;
    color: #666666;
    background-color: #ffffff;
  }

  .Cancel_Done {
    // padding: 20px;
    //     margin-left: 469px;
    //     display: flex;
    //     flex-direction: row;

    $bgColor: #e61f1d;
    $textColor: #e61f1d;
    padding: 20px;
    flex-direction: row;
    width: 100%;
    height: 57px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: 14px;

    .flgt-mngt-cancel-button {
      margin-bottom: 10px;
