.search-box {
  position: relative;
  .search-icon {
    position: absolute;
    width: 18px;
    height: 26px;
    line-height: 26px;
    color: #999999;
    left: 7px;
  }
  .search-bar {
    width: 171px;
    border-radius: 10px;
    border: solid 1px #f0f0f0;
    background-color: #ffffff;
    height: 26px;
    font-family: Roboto;
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.17;
    letter-spacing: normal;
    text-align: left;
    color: #000000;
    padding-left: 28px;
  }
}
