html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.addtask-conatainer {
  position: relative; }
  .addtask-conatainer .addtask-create-icon {
    position: absolute;
    width: 12px;
    height: 22px;
    line-height: 22px;
    font-weight: bold;
    color: #fff;
    left: 15px;
    font-size: 12px; }

.addtask-content {
  width: 100%;
  height: 22px;
  border-radius: 20px;
  color: #ffffff;
  background: linear-gradient(to bottom, #e6221a, #ff125b);
  height: 22px;
  font-size: 12px;
  font-family: 'Roboto';
  font-stretch: normal;
  font-style: normal;
  line-height: 22px-3;
  letter-spacing: normal;
  text-align: left;
  padding-left: 20px;
  border: transparent; }
