/* Accordion styles */
.tabs {
  width: 100%;
  overflow: hidden; }

.tab {
  width: 100%;
  color: #666666;
  overflow: hidden; }
  .tab .slider .minus {
    display: none; }
  .tab-label {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    font-weight: 500;
    align-items: center;
    padding: 0px 18px;
    border: solid 1px #d9d9d9;
    cursor: pointer;
    border-radius: 10px;
    padding: 12px 19.5px 12px 18px;
    background-color: #ffffff; }
  .tab-content {
    padding: 0 1em;
    color: #000000;
    border: solid 1px #d9d9d9;
    -webkit-transition: all 0.35s;
    transition: all 0.35s;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px; }
  .tab-close {
    display: flex;
    justify-content: flex-end;
    padding: 1em;
    font-size: 0.75em;
    cursor: pointer; }

.tab .active-slide .tab-label {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  background-color: #f8f8f8; }

.tab .active-slide .add {
  display: none; }

.tab .active-slide .minus {
  display: block; }

.heading + .tab-label {
  border: solid 1px #d9d9d9;
  border-top-left-radius: solid 1px #d9d9d9;
  border-top-right-radius: solid 1px #d9d9d9;
  background-color: #f8f8f8; }
  .heading + .tab-label::after {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg); }

.heading ~ .tab-content {
  max-height: 100%;
  overflow: hidden !important;
  border: solid 1px #d9d9d9;
  -webkit-transition: height 0.5s;
  transition: height 0.5s;
  max-height: 600px; }

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-10%);
            transform: translateY(-10%); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-10%);
            transform: translateY(-10%); }
  100% {
